<template>
  <section>
    <!-- 最新音乐标题 -->
    <TitleTips><slot></slot></TitleTips>

    <!-- 最新音乐列表 -->
    <ul class="newestlist">
      <li v-for="(item, index) in newestlistdata" :key="index">
        <NewestSongItem
          :itemdata="item"
          :activemusic="activemusic"
          :paused="paused"
          :isicon="isicon"
          @update:activemusic="$emit('update:activemusic', { item, index })"
          @aggrallsong="$emit('aggrallsong')"
        />
      </li>
    </ul>
  </section>
</template>

<script>
import TitleTips from "./TitleTips";
import NewestSongItem from "./NewestSongItem";

export default {
  props: {
    newestlistdata: {
      type: Array,
    },
    activemusic: {
      type: Object,
    },
    paused: {
      type: Boolean,
    },
    isicon: Boolean
  },
  components: {
    TitleTips,
    NewestSongItem,
  },
};
</script>

