<template>
  <div id="app">
    <Loading />
    

    <Home v-show="$route.meta.isShowHome" />

    <keep-alive>
      <router-view
        :activemusic="activemusic"
        :paused="paused"
        :isicon="isicon"
        @update:activemusic="
          activemusic = $event.item;
          activeindex = $event.index;
        "
        @aggrallsong="setmusic"
        v-show="!this.$root.isshowloading"
      />
    </keep-alive>

    <Play
      :activemusic="activemusic"
      :activeindex="activeindex"
      :aggrallsong="aggrallsong"
      :randommusic="randommusic"
      @update:pause="paused = $event"
      @upaggrallsong="
        activemusic = $event.item;
        activeindex = $event.index;
      "
      @update:delmusic="delmusic"
      @update:index="activeindex = $event"
      v-show="activemusic.id"
    ></Play>
  </div>
</template>

<script>
import Home from "@/views/Home.vue";
import Play from "@/components/Play.vue";

export default {
  data: function () {
    return {
      activemusic: {
        name: "...",
        picUrl: "",
        song: {
          artists: [{ name: "........." }],
        },
      },
      activeindex: 0,
      aggrallsong: null,
      randommusic: null,
      paused: false,
      isicon: true,
    };
  },
  methods: {
    setmusic(allsong){
      this.aggrallsong = allsong;
      this.randommusic = allsong.map((item) => {
        return { ...item };
      });
      this.randommusic = this.randommusic.sort(function () {
        return Math.random() - 0.5;
      });
    },
    delmusic(id){
      this.aggrallsong = this.aggrallsong.filter(item=>{
        return item.id != id;
      });
      this.randommusic = this.randommusic.filter(item=>{
        return item.id != id;
      });

      if(this.activeindex != 0) this.activeindex--;
      // this.activeindex = this.aggrallsong.findIndex(item=>{
      //   return item.id == this.activemusic.id;
      // });
    }
  },
  components: {
    Home,
    Play,
  },
};
</script>

