<template>
  <div class="bottomer">
    <div class="progress-bar">
      <p>{{ currentTime }}</p>
      <div class="prog-bg">
        <i :style="{ width: (songcurrentTime / songalltime) * 100 + '%' }"></i>
        <input
          type="range"
          min="0"
          :max="songalltime"
          step="1"
          @input="setCurrentTime"
          :value="songcurrentTime"
        />
      </div>
      <p>{{ alltime }}</p>
    </div>
    <div class="playpagecontrol">
      <ul>
        <li
          class="iconfont"
          v-html="this.activeMode.mode"
          @click="toggleMode"
        ></li>
        <li class="iconfont" @click="$emit('previous-music')">&#xe610;</li>
        <li
          @click="$emit('play-stop')"
          :class="{ playbtntoggle: !paused }"
        ></li>
        <li class="iconfont" @click="$emit('next-music')">&#xe611;</li>
        <li @click.stop="isshowpage = !isshowpage" class="iconfont">
          &#xe607;
        </li>
      </ul>
    </div>
    <WaitForList
      @actmsc="$emit('actmsc', $event)"
      @update:chongfu-next="$emit('update:chongfu-next')"
      @update:nextmsc="$emit('next-music')"
      @update:delmusic="$emit('update:delmusic',$event)"
      v-bind:isshowpage.sync="isshowpage"
      :aggrallsong="aggrallsong"
      :activemusic="activemusic"
      :playmode="playmode"
    />
  </div>
</template>

<script>
import WaitForList from "@/components/WaitForList.vue";

export default {
  props: [
    "songalltime",
    "songcurrentTime",
    "paused",
    "aggrallsong",
    "activemusic",
    "playmode",
  ],
  data: function () {
    return {
      activeMode: { idx: 0, mode: "&#xe61f;" },
      PlaybackMode: ["&#xe61f;", "&#xe802;", "&#xe71f;", "&#xe66d;"],
      isshowpage: false,
    };
  },
  computed: {
    currentTime: function () {
      return (
        (Math.floor(this.songcurrentTime / 60) < 10
          ? "0" + Math.floor(this.songcurrentTime / 60)
          : Math.floor(this.songcurrentTime / 60)) +
        ":" +
        (Math.floor(this.songcurrentTime % 60) < 10
          ? "0" + Math.floor(this.songcurrentTime % 60)
          : Math.floor(this.songcurrentTime % 60))
      );
    },
    alltime: function () {
      return (
        (Math.floor(this.songalltime / 60) < 10
          ? "0" + Math.floor(this.songalltime / 60)
          : Math.floor(this.songalltime / 60)) +
        ":" +
        (Math.floor(this.songalltime % 60) < 10
          ? "0" + Math.floor(this.songalltime % 60)
          : Math.floor(this.songalltime % 60))
      );
    },
  },
  methods: {
    setCurrentTime: function (e) {
      this.$emit("setCurrentTime", e.target.value);
    },
    toggleMode: function () {
      this.activeMode.idx =
        (this.activeMode.idx + 1) % this.PlaybackMode.length;
      this.activeMode.mode = this.PlaybackMode[this.activeMode.idx];

      // 该代码在此函数内需要最后执行
      this.$emit("togglemode", this.activeMode.mode);
    },
  },
  components: {
    WaitForList,
  },
};
</script>

<style  lang="less">
.bottomer {
  height: 85px;
  .progress-bar {
    display: flex;
    align-items: center;

    padding: 0 14px;
    font-size: 13px;

    div.prog-bg {
      flex-grow: 1;
      position: relative;
      margin: 0 6px;
      height: 3px;
      background: rgba(235, 228, 228, 0.4);
      border-radius: 12px;

      i {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 0;
        height: 100%;
        background: #fd5e53;
        border-radius: 12px;

        &::after {
          content: "";
          display: block;
          width: 10px;
          height: 10px;
          background: #fff1f5;
          border-radius: 50%;

          position: absolute;
          top: 50%;
          right: 0;

          transform: translate(50%, -50%);
        }
      }
      input {
        position: absolute;
        left: 0;
        top: 0px;
        width: 100%;
        height: 100%;
        opacity: 0;
      }
    }
  }
  .playpagecontrol {
    padding: 15px 0 17px 0;
    ul {
      display: flex;
      align-items: center;
      justify-content: center;
      li {
        font-size: 22px;

        &:nth-child(2) {
          margin: 0 12px 0 30px;
        }
        &:nth-child(3) {
          width: 41px;
          height: 42px;
          background: url("https://s2.music.126.net/style/web2/img/frame/playbar.png?c5e4da1ee05b1d56461f3e12a1c81865")
            no-repeat;
          background-size: 182.4px 739.2px;
          background-position: -1px -246px;
        }
        &:nth-child(4) {
          margin: 0 30px 0 12px;
        }
      }
    }
  }
}
.playbtntoggle {
  background-position: -1px -199px !important;
}
</style>