<template>
  <div
    @click="$emit('update:isshowpage', !isshowpage)"
    v-show="isshowpage"
    class="wflist-bg"
  >
    <div @click.stop class="waitforlist">
      <div class="title-h">
        <h2>当前播放</h2>
        <p>({{ aggrallsong ? aggrallsong.length : 0 }})</p>
        <!-- <i>✘</i> -->
      </div>
      <div class="list">
        <ul>
          <li
            @click="$emit('actmsc', {item,index})"
            v-for="(item, index) in aggrallsong"
            :key="index"
          >
            <p :class="{activeitem: item.id == activemusic.id}">
              {{ item.name }} -
              <span v-for="(item, index) in (item.song.artists || item.ar)" :key="index">
                <template v-if="index"> / </template>
                {{ item.name }}
              </span>
            </p>
            <span @click.stop="delmsc(item)">×</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["isshowpage", "aggrallsong","activemusic","playmode"],
  methods: {
    delmsc(itm){
      if(this.activemusic.id == itm.id){
        
        if(this.playmode == "&#xe66d;"){
          this.$emit('update:chongfu-next');
          // 此处作用：单曲循环删除时。不会下一首。而是重复播放
        }else{
          this.$emit('update:nextmsc');
          // 其他播放模式
        }
      }
      this.$emit('update:delmusic',itm.id);
    }
  }
};
</script>

<style scoped lang="less">
.wflist-bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;

  display: flex;
  justify-content: center;
  align-items: flex-end;

  padding-bottom: 20px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: #333;

  .waitforlist {
    padding: 0 16px;
    width: 90%;
    height: 60%;
    background: white;
    border-radius: 20px;
    font: 14px/1.5 Helvetica, sans-serif, STHeiTi;

    .title-h {
      display: flex;
      align-items: center;
      margin: 16px 0;
      font-size: 18px;

      h2 {
        font-weight: bold;
      }
      p {
        color: #666;
        font-size: 14px;
        flex-grow: 1;
      }
    }
    .list {
      height: calc(100% - 60px);
      overflow: scroll;
      &::-webkit-scrollbar {
        display: none;
      }
      li {
        display: flex;
        justify-content: space-between;
        line-height: 42px;

        p {
          width: 60%;
          font-size: 16px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;

          &.activeitem {
            color: rgb(255, 27, 27);
          }
          span {
            font-size: 12px;
          }
        }
        > span {
          padding-left: 10px;
          font-size: 20px;
        }
      }
    }
  }
}
</style>