<template>
  <li class="song-item" @click="onaggrpage">
    <div class="aggrimg">
      <img :src="itemdata.picUrl" />
      <span class="number">{{ playnumber }}</span>
    </div>
    <p>{{ itemdata.name }}</p>
  </li>
</template>

<script>
export default {
  name: "SongAggrItem",
  props: {
    itemdata: Object,
  },
  computed: {
    playnumber: function () {
      return (this.itemdata.playCount / 10000).toFixed(1) + "万";
    },
  },
  methods: {
    onaggrpage: function () {
      this.$root.isshowloading = true;

      // 跳转到歌单页面
      this.$router.push({
        path: "/musicaggr",
        query: { id: this.itemdata.id },
      });

    },
  },
};
</script>

<style lang="less">
li.song-item {
  flex: 1 1 calc((100vw - 6px) / 3);
  margin-bottom: 16px;
  &:nth-child(3n + 2) {
    margin: 0 3px;
    margin-bottom: 16px;
  }

  .aggrimg {
    position: relative;
    min-height: calc((100vw - 6px) / 3);
    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;

      width: 100%;
      height: 20%;
      font-size: 0;
      background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.25),
        rgba(0, 0, 0, 0)
      );
    }

    img {
      width: 100%;
      height: 100%;
    }

    span.number {
      display: block;
      background: url("data:image/svg+xml;base64,")
        no-repeat left center;
      background-size: auto 80%;
      padding-left: 16px;

      color: white;
      font-size: 12px;

      position: absolute;
      top: 5px;
      right: 5px;
      z-index: 9;
    }
  }

  p {
    font: 14px/1.5 Helvetica, sans-serif, STHeiTi;
    overflow: hidden;
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2; /*要显示的行数*/
    -webkit-box-orient: vertical;

    padding: 0 5px;
    line-height: 1.2;
    font-size: 13px;
  }
}
</style>