<template>
  <div class="music-text-item" @click="plmusic">
    <div class="bigbox">
      <!-- 左侧歌曲信息 -->
      <div class="lefter">
        <p>{{ itemdata.name }}</p>

        <p>
          <!-- 音质 -->
          <span v-if="isicon" class="tone-quality"></span>
          <!-- 演唱歌手 -->
          <span v-for="(item, index) in itemdata.song.artists" :key="index">
            <template v-if="index"> / </template>{{ item.name }}
          </span>
          <!-- 歌名 -->
          - {{ itemdata.name }}
        </p>
      </div>

      <!-- 右侧播放按钮区域 -->
      <div class="righter">
        <span class="play-icon" v-if="activemusic.id != itemdata.id"></span>
        <div class="playstop" v-else-if="paused"></div>
        <div class="playing" v-else></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["itemdata", "activemusic", "paused", "isicon"],
  methods: {
    plmusic: function () {
      this.$emit("update:activemusic");
      this.$emit("aggrallsong");
    },
  },
};
</script>

<style lang="less">
.music-text-item {
  font-size: 17px;
  padding-left: 10px;
  color: #333;

  .bigbox {
    display: flex;
    padding-right: 10px;
    border-bottom: 1px solid rgba(218, 210, 210, 0.2);

    .lefter {
      flex-grow: 1;
      padding: 7px 0;
      padding-right: 10px;
      overflow: hidden;

      p {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        word-break: normal;
      }

      span.tone-quality {
        display: inline-block;
        vertical-align: middle;
        width: 12px;
        height: 8px;
        background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=")
          no-repeat;
        background-size: 166px 97px;
        margin-right: 5px;
        margin-bottom: 2px;
      }

      p:last-child {
        color: #888;
        font-size: 12px;
      }
    }
    .righter {
      display: flex;
      align-items: center;
      width: 24px;

      span.play-icon {
        display: block;
        width: 24px;
        height: 24px;
        background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=")
          no-repeat;
        background-size: 166px 97px;
        background-position: -23px 0;
      }
      div.playstop {
        width: 24px;
        height: 24px;
        background: url("https://s2.music.126.net/style/web2/img/table.png?9e9db2fd734e6a672d5410eb68fa65d6")
          no-repeat;
        background-size: 156px 481px;
        background-position: -25px -166px;
      }
      div.playing {
        width: 24px;
        height: 24px;
        background: url("data:image/gif;base64,") no-repeat;
        background-size: 100%;
      }
    }
  }
}
</style>