<template>
  <div class="recommendedmusic" style="margin-top: 146px">
    <SongAggrList :aggrlistdata="aggrlist" @aggrsong="$emit('aggrsong',$event)">推荐歌单</SongAggrList>

    <NewestSongList
      :newestlistdata="newestlist"
      :activemusic="activemusic"
      :paused="paused"
      :isicon="isicon"
      @update:activemusic="$emit('update:activemusic', $event)"
      @aggrallsong="$emit('aggrallsong',newestlist)"
      >最新音乐</NewestSongList
    >
  </div>
</template>

<script>
// @ is an alias to /src
import SongAggrList from "@/components/SongAggrList.vue";
import NewestSongList from "@/components/NewestSongList.vue";

export default {
  props: {
    activemusic: {
      type: Object,
    },
    paused: {
      type: Boolean,
    },
    isicon: Boolean
  },
  data: function () {
    return {
      aggrlist: null,
      newestlist: null,
    };
  },
  components: {
    SongAggrList,
    NewestSongList,
  },
  created: function () {
    this.$root.isshowloading = true;
    this.axios("/personalized?limit=6").then((response) => {
      this.aggrlist = response.data.result;
    });

    this.axios("/personalized/newsong").then((response) => {
      this.newestlist = response.data.result;

      this.$root.isshowloading = false;
    });
  },
};
</script>
