<template>
  <div class="mainer">
    <div v-show="istgl" class="music-fengmian">
      <div @click="lrctoggle" class="song-img" :class="{ imgrotate: paused }">
        <img :src="activemusic.picUrl" />
        <i></i>
      </div>
      <div class="iconlist">
        <span class="iconfont">&#xe617;</span>
        <span class="iconfont">&#xe60f;</span>
        <span class="iconfont">&#xe609;</span>
        <span class="iconfont">&#xe60d;</span>
        <span class="iconfont">&#xe670;</span>
      </div>
    </div>

    <div v-show="!istgl" class="music-geci" @click="istgl = !istgl">
      <ul
        ref="lrcbox"
        @touchstart="startTz"
        @touchmove.prevent="inTz"
        @touchend="endTz"
      >
        <li v-if="isyc">暂无歌词</li>
        <li
          v-for="(item, index) in lrclist"
          :key="index"
          :class="{ actbox: index == activeId - 1,jixubox: index == jixuid }"
        >
          {{ item.lrc }}
        </li>
      </ul>
      <div class="onplay" v-if="isjixu" @click.stop="playclick">
        <i>◮</i>
        <span></span>
        <span>{{ jixutext }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activemusic: {
      type: Object,
    },
    paused: {
      type: Boolean,
    },
    currentTime: Number,
    iscontrol_page: Boolean,
  },
  data() {
    return {
      istgl: true,
      lrcReg: /^\[(\d{2}:\d{2}\.\d{1,4})\](.+)$/,
      activeId: null,
      lrclist: [],
      isyc: false,
      isjixu: false,
      jixutext: null,
      jixuid: null,
      starY: null,
      starTop: null,
    };
  },
  methods: {
    lrctoggle: function () {
      this.istgl = !this.istgl;

      window.setTimeout(() => {
        let alltop = 0;

        if (this.activeId == -1 || this.activeId == 0) return;

        for (var i = 0; i <= this.activeId; i++) {
          alltop += this.$refs.lrcbox.children[i].offsetHeight;
        }

        this.$refs.lrcbox.style.top = 200 - alltop + "px";
      }, 200);

      // console.log(this.$refs.lrcbox.children[10].clientHeight);
      // console.log(200 - alltop);
      // console.log(this.$refs.lrcbox.style.top);
    },
    lrccl: function (text) {
      this.lrclist = [];

      let arr = text.split("\n"); //原歌词文本已经换好行了方便很多，我们直接通过换行符“\n”进行切割
      let row = arr.length; //获取歌词行数
      for (let i = 0; i < row; i++) {
        let temp_row = arr[i]; //现在每一行格式大概就是这样"[00:04.302][02:10.00]hello world";
        let temp_arr = temp_row.split("]"); //我们可以通过“]”对时间和文本进行分离
        let text = temp_arr.pop(); //把歌词文本从数组中剔除出来，获取到歌词文本了！
        //再对剩下的歌词时间进行处理
        temp_arr.forEach((element) => {
          let obj = {};
          let time_arr = element.substr(1, element.length - 1).split(":"); //先把多余的“[”去掉，再分离出分、秒
          let s = parseInt(time_arr[0]) * 60 + parseFloat(time_arr[1]); //把时间转换成与currentTime相同的类型，方便待会实现滚动效果
          obj.time = s;
          obj.lrc = text;
          this.lrclist.push(obj); //每一行歌词对象存到组件的lyric歌词属性里
        });

        this.lrclist.sort(this.sortRule); //由于不同时间的相同歌词我们给排到一起了，所以这里要以时间顺序重新排列一下
      }
    },
    sortRule(a, b) {
      //设置一下排序规则
      return a.time - b.time;
    },
    startTz(e) {
      this.starY = e.touches[0].clientY;
      this.starTop = this.$refs.lrcbox.offsetTop;
    },
    inTz(e) {
      this.isjixu = true;
      this.$refs.lrcbox.style.transition = "none";

      // 向下拉为负数。向下拉为正数
      let direction = this.starTop + e.touches[0].clientY - this.starY;

      // 判断歌词的上下边界
      if (direction > 200) {
        direction = 200;
      } else if (direction < -this.$refs.lrcbox.offsetHeight + 200) {
        direction = -this.$refs.lrcbox.offsetHeight + 200;
      }

      this.$refs.lrcbox.style.top = direction + "px";

      this.cpdactive(direction);
    },
    endTz() {
      window.setTimeout(() => {
        this.isjixu = false;
        this.jixuid = null;
      }, 1400);
      this.$refs.lrcbox.style.transition = "all 0.5s";
    },
    cpdactive: function (postop) {
      for (var i = 0; i < this.lrclist.length; i++) {
        if (postop == 200) {
          if(i==0) return;

          this.jixuid = i-1;

          let fen =
            Math.floor(this.lrclist[i - 1].time / 60) < 10
              ? "0" + Math.floor(this.lrclist[i - 1].time / 60)
              : Math.floor(this.lrclist[i - 1].time / 60) < 10;
          let miao =
            Math.floor(this.lrclist[i - 1].time) % 60 < 10
              ? "0" + (Math.floor(this.lrclist[i - 1].time) % 60)
              : Math.floor(this.lrclist[i - 1].time) % 60;

          this.jixutext = fen + ":" + miao;
        }

        postop += this.$refs.lrcbox.children[i].offsetHeight;
      }
    },
    playclick(){
      this.$emit('update:time',this.lrclist[this.jixuid]);
      this.isjixu = false;
      this.jixuid = null;
      this.$refs.lrcbox.style.transition = "all 0.5s";
    }
  },

  watch: {
    activemusic: function (n) {
      this.$emit("set-act-time");
      this.activeId = null;
      this.$refs.lrcbox.style.top = "200px";
      this.axios("/lyric?id=" + n.id).then((response) => {
        this.isyc = false;

        if (!response.data.lrc) {
          this.isyc = true;
          this.lrclist = [];
          return;
        }

        // this.lrclist = response.data.lrc.lyric.split("\n").map((item) => {
        //   let arr = this.lrcReg.exec(item.trim());
        //   arr = arr ? { time: arr[1], lrc: arr[2] } : { lrc: "." };

        //   let brr = null;
        //   if (arr.time) {
        //     brr = arr.time.split(":");
        //     arr.time = parseInt(brr[0]) * 60 + parseFloat(brr[1]) + 0.3;
        //   }

        //   return arr;
        // });

        this.lrccl(response.data.lrc.lyric);
      });
    },
    currentTime: function (n) {
      this.activeId = this.lrclist.findIndex((item) => {
        return item.time >= n;
      });
      // this.lrclist.forEach((item,index)=>{
      //   if(item.time >= n){
      //     this.activeId = index;
      //     break;
      //   }
      // });
      // console.log(n,this.activeId);
    },
    activeId: function (n) {
      if (this.istgl || this.iscontrol_page || this.isjixu) return;

      let alltop = 0;

      if (n == -1 || n == 0) return;

      for (var i = 0; i <= n; i++) {
        alltop += this.$refs.lrcbox.children[i].offsetHeight;
      }

      this.$refs.lrcbox.style.top = 200 - alltop + "px";
    },
  },
};
</script>

<style scoped lang="less">
.mainer {
  position: relative;
  box-sizing: border-box;
  height: calc(100% - 52px - 85px - 60px);
  margin-top: 40px;
  margin-bottom: 20px;
  overflow: hidden;

  .music-fengmian {
    height: 100%;
  }

  div.song-img {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 296px;
    height: 296px;
    margin: 0 auto;

    img {
      width: 184px;
      height: 184px;
      border-radius: 50%;
    }

    i {
      position: absolute;
      top: 0;
      z-index: 2;
      display: block;
      width: 100%;
      height: 100%;
      background: url("https://p6.music.126.net/obj/wo3DlcOGw6DClTvDisK1/4204002263/4d50/4866/ad46/996fc8a2bc62e1ab3f51f135fc459577.png?imageView=&quality=75&thumbnail=200y200&tostatic=0")
        no-repeat;
      background-size: 100% 100%;
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;

      width: 100%;
      height: 100%;
      background: url("https://s3.music.126.net/mobile-new/img/disc.png?d3bdd1080a72129346aa0b4b4964b75f=")
        no-repeat;
      background-size: 100%;
      transform: translate(-50%, -50%);
    }
    animation: musicimgrotate 20s linear infinite;
  }

  div.iconlist {
    position: absolute;
    bottom: 0;

    display: flex;
    padding: 0 24px;
    width: 100%;

    span {
      display: block;
      flex: 1 1 20vw;
      font-size: 24px;
      text-align: center;
    }
  }

  .music-geci {
    text-align: center;
    position: relative;
    height: 85%;
    margin-top: 5%;
    margin-bottom: 10%;
    overflow: hidden;

    ul {
      position: absolute;
      left: 0;
      top: 200px;
      z-index: 9;
      width: 100%;
      transition: all 0.6s;
      color: rgb(202, 202, 202);

      li {
        padding: 11px 6px;
        line-height: 1.4;

        &.actbox {
          font-family: "楷体";
          font-size: 17px;
          text-shadow: 0 0 1px #ff0000;
          color: #ff4040;
          transform: scale(1.2);
        }
        &.jixubox{
          color: #5ddfff;
        }
      }
    }

    .onplay {
      position: absolute;
      top: 200px;
      left: 0;
      z-index: 99;

      display: flex;
      align-items: center;
      width: 100%;
      padding: 6px 8px;
      color: #e0d8d8;
      transform: translateY(-100%);

      i {
        font-size: 24px;
        font-style: normal;
        transform: rotate(90deg);
      }
      span:nth-child(2) {
        flex-grow: 1;
        padding-top: 1px;
        margin: 0 14px;
        background: #999;
        opacity: 0.6;
      }
    }
  }
}

.imgrotate {
  animation-play-state: paused !important;
}

@keyframes musicimgrotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>