import Vue from 'vue'
import VueRouter from 'vue-router'
import RecommendedMusic from '../views/RecommendedMusic.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'RecommendedMusic',
    component: RecommendedMusic,
    meta: {isShowHome: true}
  },
  {
    path: '/hotmusic',
    name: 'HotMusic',
    component: () => import(/* webpackChunkName: "about" */ '../views/HotMusic.vue'),
    meta: {isShowHome: true}
  },
  {
    path: '/searchbox',
    name: 'SearchBox',
    component: () => import(/* webpackChunkName: "about" */ '../views/SearchBox.vue'),
    meta: {isShowHome: true}
  },
  {
    path: '/musicaggr',
    name: 'MusicAggr',
    component: () => import(/* webpackChunkName: "about" */ '../views/MusicAggr.vue'),
  }
]

const router = new VueRouter({
  routes
})

export default router
