<template>
  <div class="play">
    <audio
      class="songplay"
      :src="musicurl"
      controls
      autoplay
      ref="audioer"
      v-show="false"
    ></audio>

    <!-- 播放控件 -->
    <transition
      name="playcontrol"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <div class="playcontrol" v-show="iscontrol_page">
        <img
          :src="
            activemusic.picUrl ? activemusic.picUrl + '?param=100y100' : null
          "
          @click="iscontrol_page = !iscontrol_page"
        />
        <div class="songdata" @click="iscontrol_page = !iscontrol_page">
          <h5>{{ activemusic.name }}</h5>
          <p>
            <span
              v-for="(item, index) in activemusic.song.artists"
              :key="index"
            >
              <template v-if="index"> / </template>{{ item.name }}
            </span>
          </p>
        </div>

        <div class="playbtn" @click="toggle_bofang">
          <canvas width="50px" height="50px" ref="canvaser"></canvas>
          <!-- <i v-if="paused" style="background-position: -25px -165px"></i>
          <i v-else></i> -->
          <span v-if="paused" class="iconfont icon-bofang"></span>
          <span v-else class="iconfont icon-zanting"></span>
        </div>
      </div>
    </transition>

    <transition
      name="playpage"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <div class="playpage" v-show="!iscontrol_page">
        <div
          class="bg-filter"
          :style="{
            'background-image': 'url(' + activemusic.picUrl + ')',
            opacity: '1',
            filter: 'blur(28px) grayscale(0.4)',
          }"
        ></div>
        <PlayPageToper
          :activemusic="activemusic"
          @toggle-iscont-page="iscontrol_page = !iscontrol_page"
        ></PlayPageToper>

        <PlayPageMainer
          :paused="paused"
          :activemusic="activemusic"
          :currentTime="currentTime"
          :iscontrol_page="iscontrol_page"
          @update:time="setCurrentTime"
          @set-act-time="$refs.audioer.currentTime = 0"
        ></PlayPageMainer>

        <PlayPageBottomer
          :songalltime="alltime"
          :songcurrentTime="currentTime"
          :paused="paused"
          :aggrallsong="aggrallsong"
          :activemusic="activemusic"
          :playmode="playmode"
          @setCurrentTime="$refs.audioer.currentTime = $event"
          @play-stop="toggle_bofang"
          @previous-music="previousmusic"
          @next-music="nextmusic"
          @togglemode="playmode = $event"
          @actmsc="$emit('upaggrallsong', $event)"
          @update:delmusic="$emit('update:delmusic', $event)"
          @update:chongfu-next="chongfunext"
        ></PlayPageBottomer>
      </div>
    </transition>
  </div>
</template>

<script>
import PlayPageToper from "./PlayPageToper.vue";
import PlayPageMainer from "./PlayPageMainer.vue";
import PlayPageBottomer from "./PlayPagebottomer.vue";

export default {
  props: {
    activemusic: {
      type: Object,
    },
    activeindex: {
      type: Number,
    },
    aggrallsong: {
      type: Array,
    },
    randommusic: {
      type: Array,
    },
  },
  data: function () {
    return {
      musicurl: "",
      iscontrol_page: true,
      paused: true,
      currentTime: 0,
      alltime: 0,
      ctx: null,
      playmode: "&#xe61f;",
    };
  },
  computed: {
    israndom: function () {
      return "&#xe71f;" == this.playmode;
    },
  },
  methods: {
    toggle_bofang: function () {
      if (!this.activemusic.id) return;

      if (this.paused) {
        this.$refs.audioer.play();
      } else {
        this.$refs.audioer.pause();
      }
    },
    // 更新音乐控件播放按钮的进度
    upplay: function () {
      let ask = ((this.currentTime / this.alltime) * 2 - 0.5) * Math.PI;
      this.ctx.clearRect(0, 0, 50, 50);

      this.ctx.beginPath();
      this.ctx.arc(25, 25, 18, 0, Math.PI * 2);
      this.ctx.closePath();
      this.ctx.strokeStyle = "rgb(235, 230, 230)";
      this.ctx.stroke();

      this.ctx.beginPath();
      this.ctx.arc(25, 25, 19, Math.PI * -0.5, ask);
      this.ctx.strokeStyle = "#333";
      this.ctx.stroke();
    },
    // 返回上一首歌的id
    previoussongidx: function () {
      let actidx;
      if (this.activeindex > 0 && this.playmode != "&#xe66d;") {
        actidx = this.activeindex - 1;
      } else if (this.activeindex == this.aggrallsong.length - 1) {
        actidx = 0;
        this.$refs.audioer.load();
      } else if (this.playmode == "&#xe802;") {
        actidx = this.aggrallsong.length - 1;
        this.$refs.audioer.pause();
      } else {
        actidx = this.aggrallsong.length - 1;
      }

      return actidx;
    },
    // 返回下一首歌的index下标
    nextsongidx: function () {
      let actidx;
      if (
        this.activeindex < this.aggrallsong.length - 1 &&
        this.playmode != "&#xe66d;"
      ) {
        actidx = this.activeindex + 1;
      } else if (this.activeindex == this.aggrallsong.length - 1) {
        actidx = 0;
        this.$refs.audioer.load();
      } else if (this.playmode == "&#xe802;") {
        actidx = this.aggrallsong.length - 1;
        this.$refs.audioer.pause();
      } else {
        actidx = 0;
      }

      // console.log(actidx, this.randommusic[actidx]);

      return actidx;
    },
    // 跳转到上一首歌
    previousmusic: function () {
      if (this.aggrallsong == 0) return;

      if (this.playmode == "&#xe66d;") {
        this.$refs.audioer.load();
        return;
      }

      let actidx = this.previoussongidx();
      this.$emit("upaggrallsong", {
        item: this.israndom
          ? this.randommusic[actidx]
          : this.aggrallsong[actidx],
        index: actidx,
      });
    },
    // 跳转到下一首歌
    nextmusic: function () {
      if (this.aggrallsong == 0) return;

      if (this.playmode == "&#xe66d;") {
        this.$refs.audioer.load();
        return;
      }

      let actidx = this.nextsongidx();
      this.$emit("upaggrallsong", {
        item: this.israndom
          ? this.randommusic[actidx]
          : this.aggrallsong[actidx],
        index: actidx,
      });
    },
    // 跳转到指定的位置开始播放
    setCurrentTime(obj) {
      if (obj) this.$refs.audioer.currentTime = obj.time + 0.2;
    },
    chongfunext() {
      let idx = this.aggrallsong.findIndex((item) => {
        return item.id == this.activemusic.id;
      });

      this.$emit("upaggrallsong", {
        item: this.aggrallsong[idx + 1],
        index: idx + 1,
      });
    },
  },
  components: {
    PlayPageToper,
    PlayPageMainer,
    PlayPageBottomer,
  },
  watch: {
    activemusic: function () {
      this.axios("/song/url?id=" + this.activemusic.id).then((response) => {
        this.musicurl = response.data.data[0].url;
      });
    },
    paused: function () {
      this.$emit("update:pause", this.paused);
    },
    // aggrallsong: function (n) {
    //   this.randommusic = n.map((item) => {
    //     return { ...item };
    //   });

    //   this.randommusic = this.randommusic.sort(function () {
    //     return Math.random() - 0.5;
    //   });
    // },
    playmode: function (n) {
      // 切换进入或退出随机模式时，重新更新歌曲在所用列表的下标
      if (n == "&#xe71f;") {
        let ind = this.randommusic.findIndex((item) => {
          return item.id == this.activemusic.id;
        });

        this.$emit("update:index", ind);
      } else if (n == "&#xe66d;") {
        let ind = this.aggrallsong.findIndex((item) => {
          return item.id == this.activemusic.id;
        });

        this.$emit("update:index", ind);
      }
    },
  },
  mounted: function () {
    let audio = this.$refs.audioer;
    // 当音频状态发生改变时触发
    audio.addEventListener("play", () => {
      this.paused = false;
    });
    audio.addEventListener("pause", () => {
      this.paused = true;
    });

    let cavs = this.$refs.canvaser;
    this.ctx = cavs.getContext("2d");
    this.ctx.beginPath();
    this.ctx.arc(25, 25, 18, 0, Math.PI * 2);
    this.ctx.closePath();
    this.ctx.lineWidth = 3;
    this.ctx.strokeStyle = "rgb(235, 230, 230)";
    this.ctx.stroke();

    // 音频播放时
    audio.addEventListener("timeupdate", () => {
      this.currentTime = audio.currentTime;
      this.upplay();
    });
    // 音频总长度发生变化时
    audio.addEventListener("durationchange", () => {
      this.alltime = audio.duration;
    });
    // 音频播放结束后
    audio.addEventListener("ended", () => {
      this.nextmusic();
    });
  },
};
</script>

<style scoped lang="less">
div.play {
  margin-bottom: 66px;

  .songplay {
    width: 100%;
    height: 40px;
  }
  div.playcontrol {
    display: flex;
    align-items: center;

    position: fixed;
    bottom: 0;
    width: 100%;
    background: #fcfbfb;
    box-shadow: 0 0 1px 0 grey;
    padding: 8px 11px;

    img {
      width: 50px;
      height: 50px;
      border-radius: 2px;
      box-shadow: 0 0 5px 0px rgb(112, 110, 110);
    }
    div.songdata {
      flex-grow: 1;
      padding: 0 8px;

      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      h5 {
        color: #333;
        font-size: 19px;
      }
      p {
        color: rgb(182, 176, 176);
        font-size: 14px;
      }
    }
    div.playbtn {
      position: relative;
      width: 50px;
      height: 50px;

      span {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 88;
        transform: translate(-49%, -50%);

        display: block;
        width: 25px;
        height: 25px;
        color: #333;
        text-align: center;
        line-height: 25px;
      }
    }
  }
  div.playpage {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;

    width: 100%;
    height: 100%;
    color: white;
    background: rgb(43, 42, 42);

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;

      display: block;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.3);
    }

    .bg-filter {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;

      width: 100%;
      height: 100%;

      transform: scale(1.1);
      background-size: auto 100%;
      background-position: center;
    }
  }
}
</style>